<template>
  <div class="oidc-container">
    <PhSpinner :size="60" style="margin-bottom: 50px">
      <animateTransform
        attributeName="transform"
        attributeType="XML"
        type="rotate"
        dur="3s"
        from="0 0 0"
        to="360 0 0"
        repeatCount="indefinite"
    /></PhSpinner>
    <p class="centered">Please complete the login process in the popup window.</p>
    <p class="centered">
      If it has not appear automatically, please
      <TypographyLink @click="login">click here</TypographyLink>.
    </p>
  </div>
</template>

<script lang="ts" setup>
import { PhSpinner } from '@phosphor-icons/vue';
import { TypographyLink } from 'ant-design-vue';
import { onMounted } from 'vue';
import { OIDCUserProvider } from './userProvider';

const emits = defineEmits<{
  (e: 'done', jwt: string): void;
}>();

const login = async () => {
  const userProvider = new OIDCUserProvider();
  const jwt = await userProvider.login();
  if (!jwt) throw new Error('[OIDC] Login did not return an user');
  emits('done', jwt);
};

onMounted(() => login());
</script>
