<template>
  <AntFlex vertical align="center" gap="middle" class="passwordless">
    <AntFlex vertical align="center" gap="small" class="header">
      <Logo hide-text size="large" :image-url="logoUrl" :brand-name="brandName" />
      <h2>
        {{ i18nProvider.translate('i18n_auth_validate_your_email_login', locale, { brandName }) }}
      </h2>
    </AntFlex>
    <AntAlert
      v-if="isLocal"
      :message="localMessage"
      type="warning"
      show-icon
      style="width: 100%; text-align: left"
    />
    <AntForm v-if="state.stage === 'collect-info'" class="section">
      <div class="description">
        {{ i18nProvider.translate('i18n_auth_info_description', locale) }}
      </div>
      <AntFormItem
        has-feedback
        :validate-status="state.invalid ? 'error' : ''"
        :help="state.invalid ? i18nProvider.translate('i18n_auth_info_invalid_email', locale) : ''"
      >
        <AntInput
          type="email"
          :value="state.info.email"
          :placeholder="i18nProvider.translate('i18n_auth_enter_your_email', locale)"
          autofocus
          @blur="validateEmail"
          @keyup.enter="() => collectInfo()"
          @change="updateEmailToLowerCase($event.target.value)"
        />
      </AntFormItem>
      <AntButton type="primary" @click="() => collectInfo()">
        {{ i18nProvider.translate('i18n_auth_info_send_code', locale) }}
      </AntButton>
    </AntForm>
    <AntForm v-else-if="state.stage === 'collect-token'" class="section">
      <div class="description">
        {{ i18nProvider.translate('i18n_auth_token_label', locale, state.info) }}
      </div>
      <AntFormItem has-feedback :validate-status="state.invalid ? 'error' : ''">
        <OTPInput
          v-model:value="state.token"
          :length="6"
          :numeric="true"
          size="large"
          @collect-token="collectToken"
        />
        <AntAlert
          v-if="state.invalid"
          :message="i18nProvider.translate('i18n_auth_token_invalid')"
          type="error"
          style="margin-top: 24px"
        />
      </AntFormItem>

      <AntButton type="primary" @click="collectToken">
        {{ i18nProvider.translate('i18n_auth_token_verify_email', locale) }}
      </AntButton>
      <AntButton @click="restartAuth">
        {{ i18nProvider.translate('i18n_auth_edit_email', locale) }}
      </AntButton>
      <AntButton :disabled="!!state.secsToAllowResend" @click="resendToken">
        {{ i18nProvider.translate('i18n_auth_token_resend_email', locale) }} ({{
          state.secsToAllowResend
        }}
        s)
      </AntButton>
      <div class="footer">
        {{ i18nProvider.translate('i18n_auth_token_footer_alternative_email', locale) }}
      </div>
    </AntForm>
    <div v-else-if="state.stage === 'loading'" class="loading">
      <LoadingIndicator />
    </div>
  </AntFlex>
</template>

<script lang="ts" setup>
import OTPInput from '@/components/inputs/OTPInput.vue';
import Logo from '@/components/Logo.vue';
import { SetttingsProvider } from '@/settings';
import { isEmail } from '@/utils/string';
import LoadingIndicator from '@/widgets-lib/common/components/CircularLoading.vue';
import { i18nProvider, SupportedLocales } from '@/widgets-lib/common/i18n';
import {
  Alert as AntAlert,
  Button as AntButton,
  Flex as AntFlex,
  Form as AntForm,
  FormItem as AntFormItem,
  Input as AntInput,
} from 'ant-design-vue';
import { computed, ref } from 'vue';
import { AuthnUserProvider } from '.';

type AuthState = {
  stage: 'collect-info' | 'collect-token' | 'loading' | 'done';
  info: { email: string };
  token: string;
  invalid: boolean;
  secsToAllowResend: number;
};

const state = ref<AuthState>({
  stage: 'collect-info',
  info: { email: '' },
  token: '',
  invalid: false,
  secsToAllowResend: 0,
});

const userProvider = new AuthnUserProvider();

const props = defineProps<{
  logoUrl?: string;
  brandName?: string;
  locale?: SupportedLocales;
}>();

const emits = defineEmits<{
  (e: 'done', jwt: string): void;
}>();

const isLocal = computed(() => SetttingsProvider.instance?.isLocal);

const localMessage = i18nProvider.translate('i18n_local_auth_info_description', props.locale);

function validateEmail(): boolean {
  const email = state.value.info.email;
  const isEmailValid = isEmail(email);

  state.value.invalid = !isEmailValid;
  return isEmailValid;
}

let interval: ReturnType<typeof setInterval>;
const collectInfo = async ({ isResend }: { isResend?: boolean } = {}) => {
  if (!validateEmail()) {
    return;
  }

  state.value.stage = 'loading';

  try {
    await userProvider.authenticate(state.value.info.email, isResend);
    state.value.stage = 'collect-token';
    state.value.secsToAllowResend = 120;
    clearInterval(interval);
    interval = setInterval(() => {
      state.value.secsToAllowResend -= 1;
      if (state.value.secsToAllowResend <= 0) {
        clearInterval(interval);
        state.value.secsToAllowResend = 0;
      }
    }, 1000);
  } catch (e: any) {
    state.value.invalid = true;
    state.value.stage = 'collect-info';
  }
};

const updateEmailToLowerCase = (value?: string) => {
  if (!value) {
    state.value.info.email = '';
    return;
  }
  state.value.info.email = value.toLowerCase();
};

const collectToken = async () => {
  if (!state.value.info.email) return;
  state.value.stage = 'loading';
  try {
    const jwt = await userProvider.verify(state.value.info.email, state.value.token);
    if (!jwt) throw new Error('[Passwordless] Login did not return an user');
    emits('done', jwt);
    state.value.stage = 'loading';
  } catch (e: any) {
    state.value.invalid = true;
    state.value.stage = 'collect-token';
  }
};

const notifyResendToken = async (email: string) => {
  await fetch(`https://admin.abstra.app/_hooks/notify-email-not-received?email=${email}`, {
    method: 'GET',
  });
};

const resendToken = () => {
  if (state.value.info.email) {
    collectInfo({ isResend: true });
    notifyResendToken(state.value.info.email);
  }
};

const restartAuth = () => {
  state.value = {
    stage: 'collect-info',
    info: { email: '' },
    token: '',
    invalid: false,
    secsToAllowResend: 0,
  };
};
</script>

