<template>
  <Oidc v-if="OIDCUserProvider.isConfigured()" @done="done" />
  <Passwordless v-else :locale="locale" :logo-url="logoUrl" :brand-name="brandName" @done="done" />
</template>

<script lang="ts" setup>
import Oidc from '@/apps/player/auth/Oidc.vue';
import Passwordless from '@/apps/player/auth/Passwordless.vue';
import { OIDCUserProvider } from '@/apps/player/auth/userProvider';
import { SupportedLocales } from '@/widgets-lib/common/i18n';
import { useUserStore } from './userStore';

defineProps<{
  logoUrl?: string;
  brandName?: string;
  locale: SupportedLocales;
}>();

const emits = defineEmits<{
  (e: 'done', jwt: string): void;
}>();

const done = (jwt: string) => {
  useUserStore().saveJWT(jwt);
  emits('done', jwt);
};
</script>
